@font-face {
  font-family: "Gilroy";
  font-display: swap;
  src: url("../../fonts/Gilroy-Regular.woff2") format("woff2"),url("../../fonts/Inter-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: "Gilroy";
  font-display: swap;
  src: url("../../fonts/Gilroy-Medium.woff2") format("woff2"),url("../../fonts/Inter-Medium.woff") format("woff");
  font-style: normal;
  font-weight: 500;
}

@font-face {
  font-family: "Gilroy";
  font-display: swap;
  src: url("../../fonts/Gilroy-Bold.woff2") format("woff2"),url("../../fonts/Inter-Bold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
dd,
dt {
  margin: 0;
}

body {
  margin: 0;
  font-family: "SFProDisplay",sans-serif;
  font-size: 16px;
  color: #000;
  height: 100%;
  min-height: 100vh;
  background: #fff;
  width: 100%;
}

main {
  flex: 1;
}

.lock {
  overflow: hidden;
}

.uppercase {
  text-transform: uppercase;
}

img {
  max-width: 100%;
  display: block;
}

a {
  text-decoration: none;
  color: #000;
  transition: .3s ease-in-out;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

button {
  padding: 0;
  border-style: solid;
  border: none;
}

svg {
  display: block;
  transition: .3s ease-in-out;
}

ul,
ol {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

input,
textarea {
  outline: none;
}

textarea {
  resize: none;
}

p {
  margin: 0;
}

input:not([type=checkbox]):not([type=radio]),
textarea,
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.popup {
  position: fixed;
  width: 335px;
  height: auto;
  background: #fff;
  box-shadow: 0px 8px 20px rgba(29,31,52,.15);
  border-radius: 10px;
  top: 50%;
  left: -100%;
  transform: translate(-50%, -50%);
  opacity: 0;
  visibility: hidden;
  transition: opacity ease-in-out .3s;
  z-index: 11;
  pointer-events: none;
}

.popup--show {
  left: 50%;
  opacity: 1;
  visibility: visible;
  transition: opacity ease-in-out .3s;
  pointer-events: all;
}

.popup__close {
  width: 24px;
  height: 24px;
  position: absolute;
  right: 5%;
  top: 5%;
  cursor: pointer;
}

.popup__close svg {
  width: 100%;
  height: 100%;
  transition: transform ease-in-out .3s;
}

.popup__content {
  padding: 70px 55px 75px 57px;
  text-align: center;
}

.popup__content--wait {
  padding: 66px 55px 57px 57px;
}

.popup__title h3 {
  margin-top: 18px;
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
}

.overlay {
  width: 100%;
  height: 100%;
  background: #f5f5f5;
  top: 0;
  left: 0;
  right: 0;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: all ease-in-out .3s;
  z-index: 10;
}

.overlay--show {
  position: fixed;
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transition: all ease-in-out .3s;
}

.bildboard {
  position: relative;
  z-index: 1;
  margin-top: 30px;
}

.bildboard__image img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  display: block;
}

.bildboard .bildboard__wrapper {
  height: 500px;
}

.bildboard .bildboard__content {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding-top: 22px;
}

.bildboard .bildboard__content h1 {
  max-width: 80%;
  font-family: "Gilroy",sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 120%;
  color: #000;
  text-align: center;
}

.bildboard .bildboard__content p {
  max-width: 65%;
  margin-top: 20px;
  font-family: "Gilroy",sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #000;
  text-align: center;
}

.bildboard .bildboard__content a {
  margin-top: 42px;
  font-family: "Gilroy",sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: .03em;
  color: #000;
  padding: 15px 34px;
  background: #fe0;
  border-radius: 100px;
  transform: all ease-in-out .3s;
}

.form {
  margin-top: 30px;
  display: flex;
  column-gap: 20%;
  justify-content: center;
  flex-wrap: wrap;
  row-gap: 30px;
}

.form__block {
  display: flex;
  flex-direction: column;
  border: 0;
  row-gap: 10px;
  margin: 0;
  padding: 0;
}

.form__block:not(:first-child) {
  justify-content: flex-end;
}

.form__title {
  font-family: "Gilroy",sans-serif;
  font-size: 18px;
  line-height: 120%;
  flex: 1 1 100%;
}

.radio,
.checkbox {
  position: relative;
  width: fit-content;
}

.radio__label,
.checkbox__label {
  font-family: "Gilroy",sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  position: relative;
  display: inline-block;
  flex-direction: row;
  padding-left: 32px;
  user-select: none;
  cursor: pointer;
}

.radio__label::before,
.checkbox__label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  display: inline-block;
  margin-right: 8px;
  border: 1px solid #000;
  border-radius: 25%;
  width: 24px;
  height: 24px;
  background-position: center center;
  background-size: 50% 50%;
  background-repeat: no-repeat;
}

.radio__label::after,
.checkbox__label::after {
  content: "";
  display: block;
  width: 12px;
  height: 12px;
  background-color: #00aefe;
  border-radius: 50%;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 6px;
  transform: translate(0, -50%);
  z-index: 2;
  transition: opacity .1s linear;
}

.radio__field,
.checkbox__field {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.radio__field:checked+label::after,
.checkbox__field:checked+label::after {
  opacity: 1;
}

.test {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
  margin-top: 30px;
}

.test__block {
  margin-top: 50px;
}

.test h1 {
  font-family: "Gilroy",sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 120%;
}

.test__title {
  font-family: "Gilroy",sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 120%;
  color: #00aefe;
}

.button {
  text-align: center;
}

.button button {
  margin-top: 30px;
  font-family: "Gilroy",sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  color: #fff;
  background: #00aefe;
  padding: 12px 24px;
  border-radius: 30px;
  cursor: pointer;
  transition: all ease-in-out .3s;
}

.list {
  margin: 30px auto 0;
  width: 100%;
}

.list__tabs {
  display: flex;
}

.list__tab {
  padding: 8px 10px;
  border: 1px solid #000;
  font-family: "Gilroy",sans-serif;
  font-size: 14px;
  line-height: 120%;
  cursor: pointer;
  border-radius: 10px 0 0 10px;
  transition: all ease-in-out .3s;
  width: 50%;
  text-align: center;
}

.list__tab:last-child {
  border-radius: 0 10px 10px 0;
}

.list__tab-active {
  background-color: #00aefe;
  color: #fff;
  border-color: #00aefe;
  user-select: none;
  cursor: default;
}

.list__cards {
  margin-top: 20px;
  display: none;
  transform: scaleY(0.8);
  opacity: 0;
  transform-origin: center top;
  animation: showContent .6s forwards;
  transition: all ease-in-out .3s;
}

.list__cards-active {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
}

.list__card {
  padding: 20px 15px;
  width: 100%;
  border-radius: 20px;
  box-shadow: 4px 4px 50px 0px #b5b1b1;
}

.list__card-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.list__card-price {
  font-family: "Gilroy",sans-serif;
  font-size: 24px;
  line-height: 120%;
  color: #00aefe;
}

.list__card-logo {
  max-width: 130px;
  width: 100%;
  height: auto;
  object-fit: cover;
}

.list__card-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 20px 20px;
  margin-top: 30px;
}

.list__card-info {
  display: flex;
  flex-direction: column;
  row-gap: 12px;
  order: 1;
}

.list__card-info span {
  display: block;
  color: #737373;
  font-family: "Gilroy",sans-serif;
  font-size: 16px;
  line-height: 1;
}

.list__card-info span:last-child {
  color: #000;
  font-weight: 500;
}

@keyframes showContent {
  0% {
    transform: scaleY(0.5);
    opacity: 0;
  }

  100% {
    transform: scaleY(1);
    opacity: 1;
  }
}

@media screen and (min-width: 320px) {
  .bildboard__image {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
  }
}

@media screen and (min-width: 450px) {
  .list__tab {
    font-size: 18px;
  }
}

@media screen and (min-width: 600px) {
  .bildboard .bildboard__content h1 {
    font-size: 36px;
  }

  .bildboard .bildboard__content p {
    font-size: 20px;
  }

  .list {
    width: 530px;
  }

  .list__card-content {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
  }

  .list__card-info:nth-child(2) {
    order: 4;
  }

  .list__card-info:nth-child(3) {
    order: 2;
  }

  .list__card-info:nth-child(4) {
    order: 3;
  }

  .list__card-info:nth-child(5) {
    order: 5;
  }

  .list__card-info:nth-child(6) {
    order: 6;
  }
}

@media screen and (min-width: 620px) {
  .list__card-logo {
    margin-right: 5%;
  }
}

@media screen and (min-width: 768px) {
  .lock {
    padding-right: 17px;
  }

  .bildboard .bildboard__content h1 {
    max-width: 65%;
  }

  .bildboard .bildboard__content p {
    max-width: 50%;
  }

  .bildboard {
    position: relative;
  }

  .bildboard .bildboard__content {
    justify-content: center;
    padding-top: 0;
  }

  .form {
    justify-content: flex-start;
  }

  .form__title {
    font-size: 24px;
  }

  .test h1 {
    font-size: 36px;
  }

  .test__title {
    font-size: 30px;
  }

  .button {
    text-align: left;
  }

  .button button {
    font-size: 20px;
  }

  .list {
    margin: 50px 0 0;
  }

  .list__tab {
    font-size: 24px;
    padding: 12px 36px;
  }
}

@media screen and (min-width: 920px) {
  .bildboard .bildboard__wrapper {
    height: 37.037037037vw;
  }
}

@media screen and (min-width: 1200px) {
  .bildboard .bildboard__content h1 {
    font-size: 54px;
    max-width: 864px;
  }

  .bildboard .bildboard__content p {
    font-size: 24px;
    max-width: 630px;
  }
}

@media screen and (min-width: 1500px) {
  .bildboard .bildboard__wrapper {
    height: 33.3333333333vw;
  }

  .bildboard .bildboard__content h1 {
    font-size: 80px;
    line-height: 99px;
  }
}

@media screen and (max-width: 375px) {
  .popup {
    width: 90%;
  }

  .popup__content--wait {
    padding: 15% 10% 15% 10%;
  }

  .popup__content {
    padding: 15% 10% 15% 10%;
  }
}

@media (hover: hover)and (pointer: fine) {
  .popup__close:hover svg {
    transform: rotate(180deg);
  }

  .bildboard .bildboard__content a:hover {
    background: #ffac0b;
  }

  .button button:hover {
    background-color: #0d84ba;
  }
}